.App {
  text-align: center;
}
body {
  background-color: rgba(194, 179, 179, 0.54);
  /* background-image: url('./icons/wcimg.jpg'); */
}
.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40px;
  width: 40px;
}

.App-header {
  /* background-color:  #053047; */
  opacity: 20;
  background-image: url('./icons/fwc_darkbluebg.png');
  height: 180px;
  padding: 20px;
  color: white;
}


.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.brackets {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  background-color: white;
}

.brackets > div {
  /* background-color: blue; */
}
.participants {
  width : 80vw;
  margin: 0 10vw;
}

.name, .location {
  justify-self: left;
}


.participantsHeader {
  width : 80vw;
  margin: 0 10vw;
  grid-template-columns: 1fr 1fr 1fr;
  display: grid;
  height:30px;
}

.match {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-bottom: 1px solid black;
  margin: 0px 10px;
}
.header {
  cursor: pointer;
  background-color: #0D83C7;
  color: white;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  height:30px;
}

.header:hover {
  background-color: white;
  color: #053047 
}

@-webkit-keyframes App-logo-spin {
  /* from { transform: rotate(0deg); }
  to { transform: rotate(45deg); } */
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  25% {-webkit-transform: rotate(45deg);transform: rotate(45deg)}
  50% {-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
  75% {-webkit-transform: rotate(45deg);transform: rotate(45deg)}
  100% {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
}

@keyframes App-logo-spin {
  /* from { transform: rotate(0deg); }
  to { transform: rotate(45deg); } */
  0% {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
  25% {-webkit-transform: rotate(45deg);transform: rotate(45deg)}
  50% {-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}
  75% {-webkit-transform: rotate(45deg);transform: rotate(45deg)}
  100% {-webkit-transform: rotate(0deg);transform: rotate(0deg)}
}

@media screen and (min-width: 600px) {
  .brackets {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    background-color: white;
  }

  .App-header {
    height: 200px;
  }
}



